import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import home from '../../assets/css/public/Navbar.module.css'
import flower from '../../assets/images/flower.svg'
import cross from '../../assets/images/zoki.svg'
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { openMenu, closeMenu } from '../../redux/actions/controller.actions.js'
class Navbar extends Component {
    constructor(props) {
        super();
        this.state = {
            initial_state : ''
        }
    }

    openMenu = () => {
        this.props.openMenu()
    }

    closeMenu = () => {
        this.props.closeMenu()

    }
    
  render() {
      console.log(this.props.state.controller.menu_active)
    return (
        <div>
        <div className={this.props.state.controller.menu_active ? home.blurred_active : home.blurred}></div>
        <div className={this.props.state.controller.menu_active ? home.navigation_active : home.navigation}>
            <h1>{this.state.current_page}</h1>
            <Link to="/"><li onClick={() => this.closeMenu()} className={`${this.props.current_page == 'Home' ? home.menu_item_active: ''}`}><img src={flower}></img>HOME</li></Link>
            <Link to="/apartments"><li onClick={() => this.closeMenu()} className={`${this.props.current_page == 'Apartments' ? home.menu_item_active: ''}`}><img src={flower}></img>CONCIERGE</li></Link>
            <Link to="/cars"><li onClick={() => this.closeMenu()} className={`${this.props.current_page == 'Cars' ? home.menu_item_active: ''}`}><img src={flower}></img>CARS</li></Link>
            <Link to="/contact"><li onClick={() => this.closeMenu()} className={`${this.props.current_page == 'Contact' ? home.menu_item_active: ''}`}><img src={flower}></img>CONTACT</li></Link>
            <Link to="/about"><li onClick={() => this.closeMenu()} className={`${this.props.current_page == 'About us' ? home.menu_item_active: ''}`}><img src={flower}></img>ABOUT US</li></Link>
            <Link to="/FAQ"><li onClick={() => this.closeMenu()} className={`${this.props.current_page == 'FAQ' ? home.menu_item_active: ''}`}><img src={flower}></img>FAQ</li></Link>

            <li onClick={() => this.closeMenu()} className={home.close_cross}><img src={cross}></img></li>
        </div>
        <Link to="/"><div className={home.logo}></div></Link>
        </div>
    )
  }
}

const mapStateToProps = state => {
    return {
      state: state
    };
  };
  
  export default withRouter(
    connect(
      mapStateToProps,
      { openMenu, closeMenu }
    )(Navbar)
  );