import React, { Component } from 'react'
import home from '../../assets/css/public/Home.module.css'
import flower from '../../assets/images/flower.svg'
import { Link } from 'react-router-dom'
import cross from '../../assets/images/zoki.svg'
import { openMenu, closeMenu } from '../../redux/actions/controller.actions'
import Navbar from './Navbar'
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";

class Home extends Component {
    constructor(props) {
        super();

        this.state = {
            current_page : 'Home'
        }
    }


    openMenu = () => {
        this.props.openMenu()
    }
    

  render() {
      let menu_open = this.props.state.controller.menu_open
    return (
      <div className={`${home.wrapper}`}>
          <Navbar current_page="Home" />
          <div className={home.landing_main}>
          <div className={home.landing_main_text}>
              <p>SAVE YOUR TIME AND GET ACCESS TO THE MOST EXCLUSIVE AND UNIQUE PLACES</p>
          </div>
          <div className={home.landing_main_buttons}>
              <a className={home.landing_main_buttons__join_us}>JOIN US</a>
              <a onClick={() => this.openMenu()}className={home.landing_main_buttons__learn_more}>LEARN MORE</a>
          </div>
          </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
    return {
      state: state
    };
  };
  
  export default withRouter(
    connect(
      mapStateToProps,
      { openMenu, closeMenu }
    )(Home)
  );