import React, { Component } from 'react'
import home from '../../assets/css/public/Apartments.module.css'
import { Link } from 'react-router-dom'
import flower from '../../assets/images/flower.svg'
import cross from '../../assets/images/zoki.svg'
import Navbar from './Navbar.js'
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { openMenu, closeMenu } from '../../redux/actions/controller.actions'
class Apartments extends Component {
    constructor(props) {
        super();

        this.state = {
            menu_open: false,
            current_page : 'Home'
        }
    }
    componentDidMount() {
       
    }

    openMenu = () => {
        this.setState({
            menu_open: true
        })
    }

    closeMenu = () => {
        this.setState({
            menu_open: false
        })
    }
  render() {
    return (
      <div className={home.wrapper}>
          <Navbar/>
          <div className={home.content}>
            
          </div>
      </div>
    )
  }
}


const mapStateToProps = state => {
    return {
      state: state
    };
  };
  
  export default withRouter(
    connect(
      mapStateToProps,
      { openMenu, closeMenu }
    )(Apartments)
  );